body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (min-width: 920px) {
  .header-phone {
    display: none;
  }

  #not-found-artista {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 180px;
  }

  #not-found-artista img {
    width: 60px;
  }

  #not-found-artista p {
    font-size: 30px;
    color: rgba(68, 71, 75, 0.902);
  }

  .main-container {
    position: relative;
  }

  header {
    position: fixed;
    display: inline-block;
    width: 18%;
    height: 100%;
    background: rgba(30, 30, 30, 1);
  }

  .header-menu {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  #nav {
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
    margin-bottom: 160px;
  }

  #nav :link {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: -25px;
    font-size: 20px;
  }

  #nav :visited p {
    color: white;
  }

  #nav :link p {
    color: white;
  }

  #nav img {
    width: 25px;
  }

  #nav :nth-child(3) img {
    width: 28px;
    border-radius: 100%;
    border: 2px white solid;
    margin-left: -30px;
  }

  #nav :nth-child(3) p {
    margin-left: -70px;
  }

  #logo-header {
    width: 150px;
  }

  #user-header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
    font-weight: 600;
    margin-left: -40px;
    justify-content: center;
  }

  #user-header img {
    border-radius: 100%;
    width: 40px;
    height: 40px;
  }

  #user-header p {
    margin-left: 15px;
  }

  .header-menu div :link {
    display: block;
    width: 140px;
    padding: 50px;
    text-decoration-color: white;
    text-decoration: none;
  }

  .loading {
    position: absolute;
    margin-left: 45%;
    margin-top: 20%;
  }

  .loading img {
    width: 100px;
  }

  .login {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(123.66deg, #4C386D 12.08%, #13001E 86.52%);
  }

  #imgBack {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .login form {
    position: relative;
    width: 700px;
    height: 400px;
    left: 25%;
    top: 20%;

    background: rgba(30, 30, 30, 0.94);
    box-shadow: 0px 0px 26px rgba(255, 255, 255, 0.2);
    border-radius: 20px;
  }

  #logo-Form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  #loginForm {
    margin-bottom: 25px;
  }

  #loginForm input {
    color: white;
    text-align: center;
    font-weight: 600;
    height: 30px;
    width: 350px;
    border-radius: 100px;
    margin-bottom: 20px;
    background: rgba(30, 30, 30, 0.94);
    border: 1px solid rgba(91, 43, 169, 1)
  }

  #loginForm input::placeholder {
    color: white;
  }

  #loginForm button {
    color: white;
    text-align: center;
    font-weight: 600;
    height: 35px;
    width: 355px;
    border-radius: 100px;
    background-color: rgba(137, 88, 163, 100%);
  }

  .container {
    position: absolute;
    display: inline-block;
    margin-left: 18%;
    width: 82%;
    height: 100%;
  }

  .container-content {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  #header-content {
    width: 1100px;
    height: 150px;
  }

  #header-content img {
    position: absolute;
    width: 100%;
  }

  #header-content form {
    position: relative;
    margin-left: 30%;
    margin-top: 5%;
  }

  #header-content form input {
    color: white;
    font-weight: 600;
    height: 32px;
    width: 300px;
    border-radius: 23px;
    background: rgba(255, 255, 255, 0.5);
    border: none;
    margin-right: 10px;
    box-sizing: border-box;
    padding: 20px;
  }

  #header-content form input::placeholder {
    color: white;
    font-weight: 600;
    padding-left: 8px;
  }

  #header-content form button {
    color: white;
    font-weight: 600;
    height: 40px;
    width: 100px;
    border-radius: 23px;
    background-color: rgb(68, 20, 93);
    border: none;
  }

  .container-albums {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    background-color: white;
  }

  #titulo-artista {
    text-align: center;
    font-size: 24px;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .album-card {
    display: flex;
    flex-direction: column;
    /* justify-content: space-around; */
    /* align-items: center; */
    width: 180px;
    /* padding: 8px; */
    margin: 8px;
    border-radius: 5px;
    /* background-color: rgb(244, 244, 244); */
  }

  .album-card img {
    width: 180px;
    border-radius: 10px;
  }

  .album-card div h3,
  h4 {
    max-width: 18ch;
    overflow: hidden;
    text-align: left;
    white-space: nowrap;
    margin-top: 8px;
  }

  #album-id {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    bottom: 15%;
    width: 90%;
    margin-left: 100px;
  }

  #album-image> :nth-child(1) {
    width: 220px;
  }

  #album-container {
    margin-left: 30px;
  }

  #album-title {
    margin-top: 30px;
    color: white;
  }

  #album-musics {
    padding: 10px;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 700px;
    height: 100%;
    /* background-color: red; */
  }

  .music-cards {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 10px;
    background-color: rgb(222, 222, 222);
    padding: 10px;
    border-radius: 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  }

  .music-cards h4 {
    width: 20%;
    text-align: center;
  }

  .music-cards img {
    border-radius: 100%;
  }

  .music-cards audio {
    width: 40%;
  }

  .music-cards label {
    margin-left: 50px;
    width: 10%;
  }

  .music-cards div {
    margin-left: -75px;
    margin-bottom: -5px;
    position: relative;
    width: 10%;
  }

  #container-favorites {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
    height: 100%;
    margin-top: -100px;
    margin-left: 8%;
  }

  #container-favorites div {
    width: 80%;
  }

  #container-favorites div h3 {
    text-align: center;
    color: white;
    margin-bottom: 100px;
  }

  #fav-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding-bottom: 30px;
  }

  #fav-container div {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  #fav-container div h4 {
    width: 70%;
    margin-left: 30px;
    margin-right: 30px;
  }

  #fav-container div audio {
    width: 1000px;
    padding-left: 20px;
  }

  #fav-container div #container-input {
    margin-bottom: 10px;
    margin-left: 1px;
    margin-right: 10px;
  }

  .container-perfil {
    position: absolute;
    display: inline-block;
    margin-left: 18%;
  }

  .container-content-perfil {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .header-content-perfil {
    width: 1120px;
    height: 150px;
  }

  .header-content-perfil img {
    position: absolute;
    width: 100%;
  }

  #perfil-container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    bottom: 15%;
    width: 80%;
    margin-left: 150px;
  }

  #perfil-image> :nth-child(1) {
    width: 220px;
    height: 220px;
    border-radius: 100%;
  }

  #dados-container {
    margin-left: 50px;
  }

  #dados-container div {
    margin-top: 150px;
    margin-left: 30px;
    color: rgb(32, 34, 37);
  }

  #dados-container div p {
    width: 490px;
    text-align: left;
    white-space: wrap;
  }

  #dados-container div button {
    height: 40px;
    width: 130px;
    margin-top: 20px;
    color: white;
    border-radius: 100px;
    font-size: medium;
    font-weight: 600;
    background-color: rgb(68, 20, 93);
  }

  #perfil-image input {
    display: block;
    margin-top: 20px;
    margin-left: 20px;
    height: 37px;
    width: 192px;
    border-radius: 2px;
  }

  #perfil-image input::placeholder {
    text-align: center;
    color: rgb(32, 34, 37);
  }

  #perfil-container form {
    margin-top: 100px;
    margin-left: 90px;
    color: rgb(32, 34, 37);
  }

  #perfil-container form input {
    height: 37px;
    width: 288px;
    border-radius: 0px;
    font-size: 18px;
  }

  #perfil-container form textarea {
    height: 106px;
    width: 490px;
    border-radius: 0px;
  }

  #perfil-container form button {
    height: 40px;
    width: 152px;
    border-radius: 100px;
    color: white;
    font-weight: 600;
    background-color: rgb(68, 20, 93);
  }

  .p-auxiliar {
    font-size: 12px;
    margin-top: -12px;
  }
}